
// variables 
$background-color: #446DFF;
$font-color: #FFFFFF;
$main-background-color: #F5F6FA;

.home{
    max-width: 100vw;
    height: 100vh;
    margin: 0 auto 0 auto;
    display: flex;
    flex-flow: column;
    
    &-header{
        background: $background-color;
        padding: 22px;
        flex: 0 0 auto;
        text-align: center;
        
        &-text{
        font-family: 'Montserrat', sans-serif;
        font-weight: 400;
        font-size: 3.5vh;
        line-height: 30px;
        letter-spacing: 0px;
        color: $font-color;
        text-transform: capitalize;
        opacity: 1;
        }


    }

    &-main{
        background-color: $main-background-color;
        width: 100%;
        flex: 1 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: space-around;

        &--chooseType{
            max-width: 45%;
            width: 50%;
            margin: auto;
            display: flex;
            align-items: flex-end;
            justify-content: flex-start;
            flex: 0 0 15%;
            text-align: left;
            color: #000000;
            text-transform: uppercase;
            opacity: 1;
            font-family: 'Montserrat', sans-serif;
            font-size: 15px;
            line-height: 19px;
            font-weight: 500;
        }

        &--conversionType{
            max-width: 47%;
            
            width: 50%;
            margin: auto;   
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            flex: 0 0 35%;
        }

        &--conversion{
            max-width: 45%;
            width: 50%;
            margin: auto;
            flex: 0 0 50%;
            flex-direction: row;
            flex-grow: 1;
            
        }
    }

    @media only screen and (max-width: 1024px) {
        &-header{
             padding: 20px;
        
        
             &-text{
                font-size: 3vh;
                line-height: 25px;
        
            }
         }

         &-main{

             &--chooseType{
                max-width: 65%;
                width: 65%;
                font-size: 14px;
                line-height: 18px;
             }

             &--conversionType{
                 max-width: 65%;
                 width: 65%;
                 margin: auto;
                 justify-content: center;
                 flex: 0 0 30%;
            
            
             }

              &--conversion{
                max-width: 65%;
                width: 60%;
            }
        }

    }

    @media only screen and (max-width: 600px) {
        &-header{
             padding: 10px;
        
        
             &-text{
                font-size: 1vh;
                line-height: 25px;
        
            }
         }
          &-main{

             &--chooseType{
                max-width: 90%;
                width: 90%;
                font-size: 14px;
                line-height: 18px;
             }

             &--conversionType{
                 max-width: 90%;
                 width: 90%;
                 margin: auto;
                 justify-content: center;
                 flex: 0 0 30%;
            
               
             }

              &--conversion{
                max-width: 90%;
                width: 90%;
            }
        }
    }
}

.length{
    background-color: #FFFFFF;

     border: 2px solid #eee;  /* JUST TO LOOK COOL */
     box-shadow: rgba(0, 0, 0, 0.06) 0px 2px 4px;
      transition: linear 0.2s all;
     transform: translate3d(0px, 0px, 0px);

    &:hover{
        background-color: #EDFDF9;
        border-color: #0EC098;
        border-style: solid;
        border-width: 2px;
        outline: none;
        box-shadow: rgba(0,0,0,0.3) 0px 4px 15px;
        transform: translate3d(0px, -1px, 0px);
        transition: linear 0.2s all;

        & span{
            color: #0EC098;
            font-weight: 700;
            opacity: 1;
        }              
    }

     &:active{
            transform: translate3d(0px, 0px, 0px);
            box-shadow: rgba(0,0,0,0) 0px 0px 0px;
        }
}

